<template lang="pug">
div
    el-card.box-card.col-md-8.offset-2
        h1.title Cp nht mt khu
    el-card.box-card.col-md-8.offset-2
        el-container
            el-main.col-md-4
                img.col-md-10.offset-1.img-respoonsive(src='http://fibo.vn/wp-content/uploads/2018/03/logofb.png')
                p.col-12.text-center.text-uppercase.font-weight-bold.displayName {{ profile.fullName }}
                p.col-12.text-center.userName ({{ profile.username }})
            el-main.col-md-8
                form(role='form')
                    div.row
                        div.col-md-12.form-group
                            label Mt khu hin ti
                            el-input(v-model='profile.oldPass',placeholder='Mt khu hin ti',type='password')
                        div.col-md-12.form-group
                            label Mt khu mi
                            el-input(v-model='profile.newPass', placeholder='Mt khu mi',type='password')
                        div.col-md-12.form-group
                            label Xc nhn mt khu
                            el-input(v-model='profile.confirmNewPass', placeholder='Xc nhn mt khu mi',type='password')
                        div.col-md-12.form-group
                            button.btn.btn-primary.btn-lg.pull-right(type='button', @click='handleEditPassword(profile)') i mt khu
</template>

<script>
import { mapGetters, mapActions, mapState } from 'vuex';

export default {
  data() {
    return {};
  },
  computed: {
    ...mapState('session', ['profile', 'fullLoading']),
  },

  methods: {
    ...mapActions('session', ['getProfile', 'updatePassword']),
    handleEditPassword(params) {
      params.status = true;
      if (params.newPass !== params.confirmNewPass) {
        params.status = false;
        params.msg = 'Mt khu nhp ko chnh xc';
      }
      if (!params.newPass || params.newPass.length < 6) {
        params.status = false;
        params.msg = 'Mt khu phi nhiu hn 6 k t';
      }
      if (!params.oldPass) {
        params.status = false;
        params.msg = 'Hy nhp mt khu c';
      }
      this.updatePassword(params);
    },
  },

  created() {
    this.getProfile();
  },
};
</script>

<style lang="scss" scoped>
.title {
  font-size: 1.2rem;
  font-weight: 700;
  margin-bottom: 0px;
}

.el-input__inner,
.el-date-editor,
.el-select,
.el-input {
  width: 100%;
}
.form-group > label {
  display: inline-block;
  max-width: 100%;
  margin-bottom: 5px;
  font-weight: 700;
}
p.displayName {
  font-size: 1.3rem;
  margin-top: 0.4em;
  margin-bottom: 1px;
}
</style>



