<template lang="pug">
div
    el-card.box-card.col-md-8.offset-2
        h1.title Cp nht ti khon
    el-card.box-card.col-md-8.offset-2
        el-container
            el-main.col-md-4
                img.col-md-10.offset-1.img-respoonsive(src='http://fibo.vn/wp-content/uploads/2018/03/logofb.png')
                p.col-12.text-center.text-uppercase.font-weight-bold.displayName {{ profile.fullName }}
                p.col-12.text-center.userName ({{ profile.username }})
            el-main.col-md-8
                form(role='form')
                    div.row
                        div.col-md-12.form-group
                            label Tn hin th
                            el-input(v-model="profile.fullName", placeholder='Tn hin th')
                        div.col-md-12.form-group
                            label Email
                            el-input(v-model='profile.email', placeholder='Email',type='email',autocomplete='email')
                        div.col-md-12.form-group
                            label in thoi
                            el-input(v-model='profile.phone', placeholder='in thoi')
                        div.col-md-12.form-group
                            label Ngy sinh
                            el-date-picker(v-model='profile.birthday',type='date')
                        div.col-md-12.form-group
                            label Gii tnh
                            el-select(v-model='profile.gender', clearable='', filterable='', placeholder='Gii tnh')
                                el-option(key='M', label='Nam', value='M')
                                el-option(key='F', label='N', value='F')
                                el-option(key='U', label='Khc', value='U')
                        div.col-md-12.form-group
                            label a ch
                            el-input(v-model='profile.address', placeholder='a ch')
                        div.col-md-12.form-group
                            label Chc v
                            el-input(v-model='profile.position', placeholder='Chc v')
                        div.col-md-12.form-group
                            button.btn.btn-primary.btn-lg.pull-right(type='button', @click='handleEditProfile(profile)') Lu
</template>

<script>
import { mapGetters, mapActions, mapState } from 'vuex';

export default {
  data() {
    return {
      user: [],
    };
  },
  computed: {
    ...mapState('session', ['profile', 'fullLoading']),
  },

  methods: {
    ...mapActions('session', ['getProfile', 'updateProfile']),

    handleEditProfile(params) {
      params.status = true;
      if (!params.phone) {
        params.status = false;
        params.msg = 'Hy in s in thoi';
      }
      if (!params.email) {
        params.status = false;
        params.msg = 'Hy in email';
      }
      if (!params.fullName) {
        params.status = false;
        params.msg = 'Hy in tn hin th';
      }
      this.updateProfile(params);
    },
  },

  created() {
    this.getProfile();
  },
};
</script>

<style lang="scss" scoped>
.title {
  font-size: 1.2rem;
  font-weight: 700;
  margin-bottom: 0px;
}

.el-input__inner,
.el-date-editor,
.el-select,
.el-input {
  width: 100%;
}
.form-group > label {
  display: inline-block;
  max-width: 100%;
  margin-bottom: 5px;
  font-weight: 700;
}
p.displayName {
  font-size: 1.3rem;
  margin-top: 0.4em;
  margin-bottom: 1px;
}
</style>



