<template lang="pug">
div(v-loading.fullscreen.lock='fullLoading')

  el-card.box-card.col-md-12
    .clearfix.row(slot='header', :style='{ background: "#6f42c1" }')
      el-container
        el-aside(width='200px') 
          span Retries
    el-main.custom
      div.col-md-12.clearfix
        div.row
          el-select(v-model='param.servieName', clearable='', filterable='', placeholder='Service Name' @change='metChange')
            el-option(v-for='item in listServiceName', :key='item.value', :label='item.label', :value='item.value')
          el-input.form-group(v-model='param.sim', placeholder='S in thoi' , @change='metChange')
          el-select.form-group(v-model='param.type', clearable='', filterable='', placeholder='Loi' , @change='metChange')
            el-option(key='MT', label='Nhn', value='MO')
            el-option(key='MO', label='Gi', value='MT')
          el-select.form-group(v-model='param.status', clearable='', filterable='', placeholder='Trng thi' , @change='metChange')
            el-option(key='1', label='Retry', value='Retry')
            el-option(key='2', label='Stop', value='Stop')
          el-input.form-group(v-model='param.inputKey', placeholder='t kha...' , @change='metChange')
          el-date-picker.form-group(v-model='daterange',type='daterange' , @change='metChange')
          el-date-picker.form-group(v-model='param.month',type='month', format="MM/yyyy",placeholder="Thng", @change='metChangeMonth(param.month)',@selected='metChangeMonth(param.month)')
    el-table(:data='retries', height='500', style='width: 100%')
      el-table-column(prop='seq', label='STT', width='60')
      el-table-column(prop='Type', label='Loi', width='180')
      el-table-column(prop='ServiceName', label='Service Name', width='180')
      el-table-column(prop='FromNumber', label='S in thoi gi', width='180')
      el-table-column(prop='ToNumber', label='S in thoi nhn', width='180')
      el-table-column(prop='MsgContent', label='Ni dung')
      el-table-column(prop='Time', label='Ngy', width='180')
      el-table-column(prop='Status', label='Trng thi',width='100')
      el-table-column(prop='', label='Action',width='200')
          template(slot-scope='scope')
            el-button(type="success",@click="Retry(scope.row)") Retry
            el-button(type="danger",@click="Delete(scope.row)") Delete

</template>

<script>
import { mapGetters, mapActions, mapState } from 'vuex';

export default {
  data() {
    return {
      daterange:'',
      param : {
        sim:'',
        type:'',
        inputKey:'',
        servieName:'',
      },
      listServiceName:[]
    };
  },
  

  computed: {
    ...mapState('retries', ['retries', 'fullLoading']),
    //...mapState('sims', ['sims', 'fullLoading']),
  },

  methods: {
    ...mapActions('retries', ['getAllRetries', 'retryForwardSMS', 'getAllServiceName', 'deleteForwardSMS']),
    // ...mapActions('sims', ['getAllSIMs']),
    metChange() {
      this.param.date = this.daterange;
      let params = this.param;
      console.log(params);
      this.getAllRetries(params);
      
    },
    metExportExcel(){
      let param = this.param;
      this.exportExcel(param);
    },
    metChangeMonth(month){
      console.log('month', month);
      if(month != null){
        let firstDay = month;
        let lastDay = new Date(firstDay.getFullYear(),firstDay.getMonth()+1, 0);
        let dateRange = [firstDay,lastDay];
        this.daterange = dateRange;
      }
      this.metChange();
    },
    metInitDateRange(){
      let lastDay = new Date();
      let firstDay = new Date(lastDay.getFullYear(),lastDay.getMonth(), 1);
      let dateRange = [firstDay,lastDay];
      this.param.date = dateRange;
    },
    Retry(data){
      console.log('data', data);
      this.retryForwardSMS(data).then((result)=>{
         console.log('result retry forward sms', result.data.data);
         if(result.data.data){
           this.$swal('','Retry thnh cng.', 'success');
         }
         else{
           this.$swal('','Retry tht bi', 'error');
         }
      });
      this.metChange();
    },
    Delete(data){
      console.log('data', data);
      this.deleteForwardSMS(data).then((result)=>{
         console.log('result delete forward sms', result);
         if(result.data.data){
           this.$swal('','Xa thnh cng.', 'success');
           this.metChange();
         }
         else{
           this.$swal('','Xa tht bi', 'error');
         }
      });
    }
  },

  created() {
    this.metInitDateRange();
    let param = this.param;
    // this.getAllSIMs();
    this.getAllRetries(param);
    
    this.getAllServiceName().then((data)=>{
      console.log('getAllServiceName', data);
      this.listServiceName.push({
        value:'',
        label:'',
      })
      data.data.data.forEach(item => {
        let itemSer={};
        itemSer.value=item.serviceName;
        itemSer.label=item.serviceName;
        this.listServiceName.push(itemSer);
      });
    });
    
  },
};
</script>

<style lang="scss" scoped>
.el-select,
.el-input,
.el-date-editor {
  margin-right: 5px;
  width: auto;
}

.el-date-editor--daterange {
  width: 260px !important;
}
.el-date-editor--month{
  width: 150px;
}

.box-card {
  /deep/ .el-range__close-icon {
    display: none;
  }

  /deep/ .el-card__header {
    padding: 0;
  }

  /deep/ .el-container {
    min-height: 36px;
  }

  /deep/ .el-card__body {
    padding: 0;
  }

  span {
    position: relative;
    top: 8px;
    padding-left: 10px;
    color: white;
    font-size: 16px;
    opacity: 0.7;
  }

  .clearfix {
    padding: 10px;
  }
  .clearfix:before,
  .clearfix:after {
    display: table;
    content: '';
  }
  .clearfix:after {
    clear: both;
  }
  .el-main{
    padding: 0px;
    overflow: inherit;
  }
}
</style>



